<template>
    <div class="w-full p-4">
        <div class="max-w-xl mx-auto min-h-screen">
            <div class="text-left col-span-4 bg-white shadow-md p-4">
                <div class="flex flex-col justify-center items-center">
                    <div class="w-8 h-8 rounded-full overflow-hidden">
                        <img src="../../../assets/image/user.svg" alt="" class="w-full h-full object-cover bg-white p-1">
                    </div>
                    <div class="text-2xl"> {{ $store.state.login.user.name ? $store.state.login.user.name :''  }} </div>
                </div>
                <hr class="border border-hoverColor">
                <div v-if=" user.phone">Phone : {{ user.phone }}</div>
                <div v-if="user.email">Email : {{ user.email }}</div>
                <div v-if=" user.gender">Gender : {{ user.gender }}</div>
                <div v-if=" user.bmdc">BMDC : {{ user.bmdc }}</div>
                <div v-if="user.medical">Medical : {{ user.medical }}</div>
                <div>Total Appointment : {{ profile.last_appointment ? profile.last_appointment.data.length:'' }}</div>
                <div class="cursor-pointer hover:text-blue-400 hover:underline" @click="toggleModal()">My Appointment Details</div>
            </div>
        </div>

<!-- Modal --->
        <div>
            <div v-if="showModal" class="overflow-x-hidden overflow-y-auto fixed inset-0 z-50 outline-none focus:outline-none justify-center items-center flex">
                <div class="relative w-auto my-6 mx-auto max-w-6xl">
                            <!--content-->
                    <div class="border-0 rounded-lg shadow-lg relative flex flex-col w-full bg-white outline-none focus:outline-none">
                            <!--header-->
                    <div class="flex items-start justify-between p-2 border-b border-solid border-slate-200 rounded-t">
                        <label class="text-2xl font-semibold text-center">
                            Appointment Details
                        </label>
                        <button class="text-3xl text-red-500" v-on:click="toggleModal()"> Ã— </button>
                        </div>
                        <!--body-->
                        <div class="relative p-2 flex-auto">
                                <div class="text-slate-500 text-lg leading-relaxed">
                                    <table class="table-auto bg-white rounded-md mx-auto w-full text-sm text-left text-gray-500">
                                        <thead class="text-xs text-gray-700 uppercase bg-gray-50">
                                            <tr class="border-r">
                                                <th  scope="col" class="px-6 py-3 border-r">SL</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Chamber</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Feedback History</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Date</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Type</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Question</th>
                                                <th  scope="col" class="px-6 py-3 border-r">Appointment Status</th>
                                                </tr>
                                            </thead>
                                        <tbody>
                                            <tr v-for="(appointment, index) in appointments.data" :key="appointment.id" scope="row" class="border-r border-b p-4">
                                                <td class="border-r border-b p-4">{{ ++index }}</td>
                                                <td class="border-r border-b p-4">
                                                    <div>Name: {{ appointment.schedule.chamber ? appointment.schedule.chamber.name :'' }}</div>
                                                    <div>Address: {{ appointment.schedule.chamber ? appointment.schedule.chamber.address :'' }}</div>
                                                </td>
                                                <td class="border-r border-b p-4">
                                                    <router-link to="/">Feedback</router-link>
                                                </td>
                                                <td class="border-r border-b p-4">
                                                    <div>{{ appointment.schedule.date }}</div>
                                                    <div>{{ appointment.schedule.time_schedule.start_time }} -  {{ appointment.schedule.time_schedule.end_time }}</div>
                                                </td>
                                                <td class="border-r border-b p-4"> {{ profile.types ? profile.types[appointment.type] :'' }}</td>
                                                <td class="border-r border-b p-4"> 
                                                    {{ appointment.questions }}
                                                </td>
                                                <td class="border-r border-b p-4" v-html="appointment.mentor ? `<strong class='text-green-500'>Completed</strong>`: appointment.schedule.date < formatDate(new Date()) ? `<strong class='text-red-500'>Missed</strong>`:`<strong class='text-yellow-500'>Pending</strong>`"></td>
                                            </tr>
                                        </tbody>
                                        </table>
                                    </div>
                            </div>
                            </div>
                        </div>
                    </div>
                <div v-if="showModal" class="opacity-25 fixed inset-0 z-40 bg-black"></div>
             </div>
        </div>
</template>
<script>
export default {
    data(){
        return{
            // open:false
            seen: false,
            showModal: false
        }
    },
    created() {
        this.$store.dispatch('profile/getProfile');
    },
    computed:{
        profileId(){
            return this.$store.state.login.user.id;
        },
        profile(){
            return this.$store.state.profile.profile;
        },
        user(){
            return this.$store.state.profile.profile.user || '';
           
        },
        appointments(){
            return this.profile.last_appointment || '';
        }

    },
    methods:{
        formatDate(input) {
            let date = new Date(input);
            let month = '' + (date.getMonth() + 1);
            let day = '' + date.getDate();
            let year = date.getFullYear();

            if (month.length < 2) {
                month = '0' + month;
            }

            if (day.length < 2) {
                day = '0' + day;
            }
                
            return [year, month, day].join('-');
        },
        toggleModal: function(){
            this.showModal = !this.showModal;
        }
    }
}
</script>